<script>
import JsSHA from 'jssha';

export default {
  name: 'Home',
  data() {
    return {
      scenicSpots: [],
      activities: [],
      popularScenicSpots: [],
      cxlScenicSpots: [],
      cxlActivities: [],
      swiperOption: {
        warningBreakpoints: {
          1: {
            slidesPerView: 1.5,
            spaceBetween: 12,
          },
          576: {
            slidesPerView: 2.1,
            spaceBetween: 15,
          },
          768: {
            slidesPerView: 3.5,
            spaceBetween: 15,
          },
          992: {
            slidesPerView: 3.5,
            spaceBetween: 20,
          },
          1200: {
            slidesPerView: 4.5,
            spaceBetween: 30,
          },
        },
        bannerBreakpoints: {
          1: {
            slidesPerView: 1.2,
            spaceBetween: 8,
          },
          576: {
            slidesPerView: 1.5,
            spaceBetween: 8,
          },
          1200: {
            slidesPerView: 2.05,
            spaceBetween: 15,
          },
        },
        spotBreakpoints: {
          1: {
            slidesPerView: 1.5,
            spaceBetween: 8,
          },
          576: {
            slidesPerView: 2.5,
            spaceBetween: 8,
          },
          1200: {
            slidesPerView: 3.5,
            spaceBetween: 20,
          },
        },
        contributeBreakpoints: {
          1: {
            slidesPerView: 1.25,
            spaceBetween: 8,
          },
          576: {
            slidesPerView: 1.5,
            spaceBetween: 8,
          },
          1200: {
            slidesPerView: 2.5,
            spaceBetween: 22,
          },
        },
      },
    };
  },
  methods: {
    // 
    getAllScenicSpotData() {
      this.axios
        .get('https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$format=JSON', {
          headers: this.getAuthorizationHeader(),
        })
        .then((res) => {
          this.scenicSpots = res.data;
          // 
          // 
          this.cxlScenicSpots = this.scenicSpots.filter(
            (spot) => spot.OpenTime
              && (spot.OpenTime.includes('')
                || spot.OpenTime.includes('')
                || spot.OpenTime.includes('')
                || spot.OpenTime.includes(''))
              && !spot.OpenTime.includes('')
              && !spot.OpenTime.includes('')
              && !spot.OpenTime.includes('')
              && !spot.OpenTime.includes(''),
          );
          this.cxlScenicSpots.forEach((cxlSpot, cxlSpotIndex) => {
            const item = this.cxlScenicSpots[cxlSpotIndex];
            item.Name = cxlSpot.Name.split('()').join('');
            item.Name = cxlSpot.Name.split('()').join('');
            item.WarningContent = `${cxlSpot.Name} `;
          });
          // 
          this.popularScenicSpots = this.scenicSpots.filter(
            (spot) => spot.Description
              && Object.keys(spot.Picture).length !== 0
              && spot.Description.includes(''),
          );
          //  City 
          this.popularScenicSpots.forEach((spot, spotIndex) => {
            if (Object.keys(spot).includes('City') === false) {
              this.popularScenicSpots[spotIndex].City = spot.Address.includes('')
                ? `${spot.Address.split('')[0]}`
                : `${spot.Address.split('')[0]}`;
            }
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
    getAllActivitiesData() {
      this.axios
        .get('https://ptx.transportdata.tw/MOTC/v2/Tourism/Activity?$format=JSON', {
          headers: this.getAuthorizationHeader(),
        })
        .then((res) => {
          const { data } = res;
          //  StartTime 
          const activitiesTime = data
            .map((activity) => activity.StartTime)
            .sort()
            .reverse();
          //  this.activities
          activitiesTime.forEach((item, index) => {
            for (let key = 0; key < data.length; key += 1) {
              if (data[key].StartTime === item) {
                this.activities[index] = data[key];
                break;
              }
            }
          });
          // 
          this.activities = [...new Set(this.activities)];
          //  cxlActivities
          this.cxlActivities = this.activities.filter(
            (activity) => (activity.Cycle && activity.Cycle.includes(''))
              || (activity.Description
                && (activity.Description.includes('')
                  || activity.Description.includes('')
                  || activity.Description.includes(''))),
          );
          this.cxlActivities.forEach((cxlActivity, cxlActivityIndex) => {
            const item = this.cxlActivities[cxlActivityIndex];
            // 
            if (cxlActivity.Description.includes('')) {
              item.cxlReason = '';
            } else if (cxlActivity.Description.includes('')) {
              item.cxlReason = '';
            } else {
              item.cxlReason = '';
            }
            item.Name = cxlActivity.Name.split('2021').join('');
            item.Name = cxlActivity.Name.split('').join('');
            item.Name = cxlActivity.Name.split('').join('');
            item.Name = cxlActivity.Name.split('[]').join('');
            item.Name = cxlActivity.Name.split('').join('');
            item.Name = cxlActivity.Name.split('[]').join('');
            item.Name = cxlActivity.Name.split('()').join('');
            item.WarningContent = `${cxlActivity.Name} ${cxlActivity.cxlReason}`;
            //  cxlActivity  Cycle 
            if (Object.keys(cxlActivity).includes('Cycle') === false) {
              item.Cycle = item.WarningContent;
            }
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // api header setting
    getAuthorizationHeader() {
      const GMTString = new Date().toGMTString();
      const ShaObj = new JsSHA('SHA-1', 'TEXT');
      ShaObj.setHMACKey(process.env.VUE_APP_PATH, 'TEXT');
      ShaObj.update(`x-date: ${GMTString}`);
      const HMAC = ShaObj.getHMAC('B64');
      const Authorization = `hmac username="${process.env.VUE_APP_API}",algorithm="hmac-sha1", headers="x-date", signature="${HMAC}"`;
      return { Authorization, 'X-Date': GMTString };
    },
  },
  computed: {
    cxlWarning() {
      // cxlScenicSpots  cxlActivities 
      //  cxlActivities  startDate  cxlScenicSpots  UpdateTime  push  this.cxlWarning
      //  cxlScenicSpots  UpdateTime  cxlActivities  startDate 
      //  this.cxlWarning  6
      const arr = [];
      if (this.cxlActivities.length !== 0 && this.cxlScenicSpots.length !== 0) {
        let o = this.cxlActivities[0];
        let n = this.cxlScenicSpots[0];
        for (let i = 0; i < 6; i += 1) {
          if (o.StartTime > n.UpdateTime) {
            arr.push(o);
            o = this.cxlActivities[i + 1];
          } else {
            arr.push(n);
            n = this.cxlScenicSpots[i + 1];
          }
        }
      }
      return arr;
    },
  },
  created() {
    this.getAllScenicSpotData();
    this.getAllActivitiesData();
  },
};
</script>
